// ======================================
//  Content
// ======================================

.content {
    @include margin($top: scut-rem($xs));
}

.about-content {
  @include margin($bottom: scut-rem($md));
}

.article-content, .about-content p {
    @include margin($bottom: scut-rem($xs));
}


h2.article-content {
    @include margin($top: scut-rem($sm), $bottom: scut-rem($xs));
    &:first-of-type {
      margin-top: 0;
    }
}

.about-content h2 {
  @include margin($top: scut-rem($md), $bottom: scut-rem($xs));
  @include typeset(venus);
  font-weight: 700;
}

ul.about-list {
  display: block;
  list-style-type: '\27F6';
  padding: 0;
  @include margin($bottom: scut-rem($md));
}

ul.article-list {
  display: block;
  list-style-type: '\2014';
  @include margin($bottom: scut-rem($xs));
  margin-top: 0;
  padding: 0;
}

.about-list li {
  margin-left: 1.5rem;
  @include margin($bottom: scut-rem($xs));
  @include padding($left: scut-rem($xs));
}

.article-list li {
  margin-left: 2rem;
  @include margin($bottom: scut-rem(calc($xs / 2)));
  @include padding($left: scut-rem(calc($xs / 2)));
}


.about-list li:last-child {
  @include margin($bottom: scut-rem($sm));
}
